.QuizList {
	padding-top: 100px;
	width: 100%;
	display: flex;
	justify-content: center;
	flex-grow: 1;
	background: linear-gradient(90deg, #fd8355 0%, #f0576c 37%, #f79cbd 100%);
}

.QuizList h1 {
	color: #ffffff;
}

.QuizList ul {
	margin: 0;
	padding: 0;
	list-style: none;
}

.QuizList li {
	margin-bottom: 10px;
}

.QuizList li a {
	text-decoration: none;
	color: #ffffff;
}

.QuizList li a:hover {
	color: #363f54;
}